        <!-- Button sticks to the bottom -->
            $2499/m
            <div class="line-through">$4998/m</div> 
          </div>
          <div class="same-height-columns__column">
        <!-- Cover -->        
            Yearly
        <!-- Content -->
            <div class="same-height-columns__content">Save $500 per month</div>
